/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------======GLOBAL STYLES======----- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
* {
  box-sizing: border-box;
}

main {
  overflow-x: hidden;
  min-height: 90vh;
}

.wrapper {
  width: 1440px;
  margin: 0 auto;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

@mixin Roboto($size) {
  font-family: "Roboto" !important;
  font-weight: $size !important;
}

@mixin Gotham($size1) {
  font-family: "Gotham" !important;
  font-weight: $size1 !important;
}
.mobile {
  display: none;
}


/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------========HEADER========------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

header{
  .desktop{
    
    .desktop_header_wrapper{
      width: 1920px;
      margin:  0 auto;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      padding: 15px 36px 15px 29px;

      .header_logo{
        width: 203px;
        height: 57px ;
        margin-right: 8px;
      }

      .desktop_nav{
        display: flex;
        align-items: center;

        .desktop_nav_items{

          a{
            @include Gotham(400);
            font-size: 16px;
            line-height: 15px;
            padding: 5px;
            margin-right: 20px;

            &:first-child{
              padding-left: 0;
            }
          }
        }
      }
 
      .desktop_other{
        display: flex;
        margin-left: auto;
        align-items: center;

        p,a{
          @include Gotham(400);
          font-size: 16px;
          line-height: 15px;
          margin-right: 17px;
        }

        a:last-child{
          margin-right: 0;
        }

        .desktop_header_callme{
          @include Gotham(400);
          font-size: 14px;
          line-height: 13px;
          margin-right: 41px;
          padding: 12px 20px;
          color: #ffffff;
          background: #61A036;
          border-radius: 7px;
        }

        .desktop_bin,.desktop_favorite,.desktop_profile{
          padding-top: 5px;
          padding-bottom: 5px;
          padding-left: 30px;
          background: url(../img/svg/header_bin.svg) left center no-repeat;
          background-size: 20px 20px;
        }
        
        .desktop_favorite{
          background: url(../img/svg/header_favorite.svg) left center no-repeat;
          background-size: 20px 20px; 
        }

        .desktop_profile{
          background: url(../img/svg/header_profile.svg) left center no-repeat;
          background-size: 23px 23px; 
        }
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------========SEARCH========------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.search {
  width: 100%;
  display: flex;
  justify-content: center;

  .wrapper {
    border: 2px solid #61a036;
    border-radius: 7px;
    display: flex;
    justify-content: space-between;
    margin: 37px 0 40px 0;

    .desktop-2 {
      display: none;
    }

    input {
      padding: 14px 23px;
      background: 0;
      border: 0;
      width: 100%;
      @include Roboto(400);
      font-size: 18px;
      line-height: 175%;

      &::placeholder {
        @include Roboto(400);
        font-size: 18px;
        line-height: 175%;
        color: #828282;
      }
    }

    a {
      background: #61a036;
      min-width: 155px;
      padding: 15px 45px;
      font-size: 18px;
      line-height: 175%;
      color: #ffffff;
      @include Gotham(400);
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* --------=======INTRO========-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.intro {  
  margin-bottom: 40px;

  .wrapper {

    .swiper-container {
      width: 100%;
      height: 425px;
      border-radius: 7px;

      .swiper-wrapper {
        width: 100%;
        height: 100%;
      }
      
      .swiper-button-next, .swiper-button-prev {

        &:after {
          display: none;
        }
      }

      .swiper-button-next {
        width: 40px;
        height: 40px;
        margin-right: 25px;

        img {
          width: 40px;
          height: 40px;
        }
      }

      .swiper-button-prev {
        width: 40px;
        height: 40px;
        margin-left: 25px;

        img {
          width: 40px;
          height: 40px; 
        }
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------=======CATEGORIES=======------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.categories {
  width: 100%;

  .wrapper {
    display: flex;
    justify-content: center;

    .categories_blk_mobile {
      display: none;
    }

    .categories_blk {
      width: 263px;
      padding: 8px 31px 21px 31px;
      display: block;
      border: 1px solid #d2d2d2;
      box-sizing: border-box;
      border-radius: 7px;
      margin-right: 31px;

      &:last-child {
        margin-right: 0;
      }

      .blk_img {
        width: 200px;
        height: 162px;
        margin-bottom: 15px;
      }

      .blk_title {
        text-align: center;
        font-size: 24px;
        line-height: 28px;
        @include Roboto(500);
        color: #333333;
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------=======WECLOME=======-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.welcome {
  margin: 60px 0 0 0;

  .wrapper {
    display: flex;
    flex-direction: column;

    .welcome_title {
      font-size: 48px;
      line-height: 45px;
      @include Gotham(500);
      text-align: center;
      padding: 0 0 30px 0;
    }

    .welcome_sub-title {

      p {
        font-size: 18px;
        line-height: 31px;
        @include Gotham(400);
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* -------=======PORULAR=======-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.popular {

  .wrapper {
    display: flex;
    flex-direction: column;

    .welcome_title {
      font-size: 48px;
      line-height: 45px;
      @include Gotham(500);
      text-align: center;
      margin: 60px 0 40px 0;
    }

    .popular_blocks {
      display: flex;
      justify-content: space-between;

      .popular_blk {
        width: 263px;
        position: relative;

        .popular_img {
          width: 260px;
          height: 230px;
          border-radius: 7px;
        }
        .popular_country {
          font-size: 18px;
          @include Gotham(500);
          line-height: 17px;
          padding: 15px 0 15px;
        }
        .popular_title {
          font-size: 24px;
          @include Gotham(500);
          line-height: 22px;
          padding-bottom: 15px;
        }
        .popular_description {
          font-size: 18px;
          @include Gotham(400);
          line-height: 17px;
          margin-bottom: 15px;
        }
        .popular_availability {
          font-size: 16px;
          @include Gotham(400);
          line-height: 15px;
          margin-bottom: 30px;
        }
        .popular_availability-bold {
          @include Gotham(700);
        }
        .popular_crossed-price {
          font-size: 18px;
          @include Gotham(400);
          line-height: 17px;
          color: #818181;
          text-decoration-line: line-through;
          padding-bottom: 10px;
        }
        .popular_price {
          font-size: 24px;
          @include Gotham(500);
          line-height: 22px;
          padding-bottom: 15px;
        }
        .popular_price-little {
          font-size: 18px;
          line-height: 17px;
        }
        .popular_buy-minimum {
          font-size: 16px;
          @include Gotham(400);
          line-height: 15px;
          padding-bottom: 15px;
        }
        .popular_button {
          width: 100%;
          display: block;
          white-space: normal;
          padding: 16px 35px;
          background-color: #61A036;
          border-radius: 7px;
          color: white;
          font-size: 16px;
          @include Gotham(400);
          line-height: 15px;
          text-align: center;
        }
      }
    }

    .popular_footer {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 120px;

      .popular-footer-p {
        font-size: 18px;
        @include Roboto(400);
        line-height: 21px;
        padding-bottom: 25px;
      }
      .popular-footer-button {
        white-space: normal;
        padding: 16px 40px;
        background-color: #61A036;
        border-radius: 7px;
        border: none;
        color: white;
        font-size: 16px;
        @include Gotham(400);
        line-height: 15px;
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------=======ADVANTAGES=======------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.advantages {
  margin: 60px 0 60px 0;

  .wrapper {
    display: flex;
    flex-direction: column;

    .advantages_blk {

      h2 {
        font-size: 48px;
        line-height: 45px;
        @include Gotham(500);
        text-align: center;
        margin: 0 0 50px 0;
      }
    }
    .advantages_blocks {

      display: flex;
      justify-content: space-between;

      .block {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 260px;

        img {
          margin: 0 0 13px 0;
          width: 53px;
          height: 53px;
        }
        p {
          font-size: 18px;
          line-height: 31px;
          @include Gotham(400);
        }
      }
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------========================------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ----------------BIN----------------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------========================------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.main_section {
  display: flex;

  .wrapper {
    display: flex;

    .content_left {
      width: 1100px;
      display: flex;
      flex-direction: column;
    }
    .content_right {
      width: 340px;
    }
  }
}

/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------==========TITLE=========------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.title {
  margin: 65px 0 65px 0;

  .title-h1 {
    font-size: 48px;
    @include Gotham(500);
    line-height: 46px;
    padding-bottom: 25px;
  }
  .title-p {
    color: #373737;
    font-size: 18px;
    @include Gotham(400);
    line-height: 31px;
  }
}

