/* --------=========1920=======-------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
@media screen and (max-width: 1920px) {
  .wrapper {
    width: 75vw;
  }

  header{
    .desktop{
      
      .desktop_header_wrapper{
        width: 100vw;
        padding: 0.781vw 1.875vw 0.781vw 1.510vw;
  
        .header_logo{
          width: 10.573vw;
          height: 2.969vw;
          margin-right: 0.417vw;
        }
  
        .desktop_nav{
  
          .desktop_nav_items{
  
            a{
              font-size: 0.833vw;
              line-height: 0.781vw;
              padding: 0.260vw;
              margin-right: 1.042vw;
            }
          }
        }
   
        .desktop_other{
  
          p,a{
            font-size: 0.833vw;
            line-height: 0.781vw;
            margin-right: 0.885vw;
          }
  
          .desktop_header_callme{
            font-size: 0.729vw;
            line-height: 0.677vw;
            margin-right: 2.135vw;
            padding: 0.625vw 1.042vw;
            border-radius: 0.365vw;
          }
  
          .desktop_bin,.desktop_favorite,.desktop_profile{
            padding-top: 0.260vw;
            padding-bottom: 0.260vw;
            padding-left: 1.563vw;
            background: url(../img/svg/header_bin.svg) left center no-repeat;
            background-size: 1.042vw 1.042vw;
          }
          
          .desktop_favorite{
            background: url(../img/svg/header_favorite.svg) left center no-repeat;
            background-size: 1.042vw 1.042vw; 
          }
  
          .desktop_profile{
            background: url(../img/svg/header_profile.svg) left center no-repeat;
            background-size: 1.198vw 1.198vw; 
          }
        }
      }
    }
  }

  .search {
    width: 75vw;
    margin: 0 auto;
  
    .wrapper {
      border-radius: 0.365vw;
      margin: 1.927vw 0 2.083vw 0;

      .desktop-2 {
        display: none;
      }
  
      input {
