              z-index: 1;
              padding: 57px 10px 23px 10px;
              transform: translateX(100%);
              transition: .3s;

              .childmenu_back{
                border-bottom: 1px solid #61A036;
                padding-left: 24px;
                position: relative;
                
                &::after{
                  position: absolute;
                  content: " ";
                  background: url(../img/svg/header_mobile_has-child.svg) center no-repeat;
                  left: 5px;
                  top: 40%;
                  transform: translateY(-50%);
                  width: 10px;
                  height: 10px;
                  transform: rotate(180deg);
                }
