                  max-height: 500px;
                  padding-bottom: 10px;
                }

                &::after{
                  transform: rotate(180deg);
                }
              }
            }
          }

          .has-child{
            padding-right: 20px;
            background: url(../img/svg/header_mobile_has-child.svg) right center no-repeat;

            a{
